body {
  background: #0e1012;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}



header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 3rem;
  background-color: var(--mainColor);
  color: var(--textColor);
}
header a {
  text-decoration: none;
  color: white;
}
.nav-logo {
  font-weight: 600;
  font-size: 2.2rem;
  text-align: center;
  font-family: "Poppins";
}
.nav-logo span {
  color: red;
}
.nav-logo i {
  font-size: 1.8rem;
}
.nav-end {
  font-style: italic;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: end;
}

.nav-btn {
  display: none;
}
nav a {
  font-size: 14px;
  color: var(--textColor);
  text-decoration: none;
  margin-right: 25px;
}

nav div {
  font-size: 14px;
  color: var(--textColor);
  text-decoration: none;
  margin-right: 25px;
}

nav div:hover {
  color: red;
}

.nav-drop {
  position: relative;
}

.nav-drop-content {
  display: none;
  position: absolute;
  margin-top: 5px;
  padding: 10px;
  background-color: #000001;
  width: auto;
  width: 6rem;
  box-shadow: rgba(255, 255, 255, 0.2) 0px 2px 8px 0px;
}

.nav-drop:hover .nav-drop-content {
  display: block;
}

.nav-d-c-link {
  display: grid;
  gap: 10px;
}

.nav-end-start {
  padding-right: 30px;
  line-height: 40px;
  border-right: rgb(122, 122, 122) 2px solid;
}
.nav-end-start > .sd {
  display: none;
}
.nav-end-end {
  padding-left: 40px;
}
.nav-end-end i {
  margin-right: 15px;
  font-size: larger;
}

nav a:hover {
  color: var(--secondaryColor);
}

