.nav-inp {
  padding: 5px 10px;
  border: none;
  border-radius: 10px;
  width: 100px;
  font-size: 14px;
}







@media only screen and (max-width: 450px) {
  header {
    height: auto;
    padding: 10px 10px 20px;
    justify-content: start;
    align-items: center;
  }

  /* header h2 {
    order: -1;
    font-size: large;
  } */
  
  .nav-btn {
    order: -2;
  }
  
  .nav-end {
    flex: 1;
    text-align: right;
  }

  .nav-logo i {
    font-size: 1.2rem;
  }
  .nav-logo {
    font-size: 1.2rem;
    text-align: start;
  }

  .nav-btn {
    margin-right: 10px;
  }

  .nav-end-start {
    padding-right: 10px;

    line-height: 30px;
  }

  .nav-end-start > .sd {
    display: block;
  }

  .nav-end-start > .ld {
    display: none;
  }

  .nav-end-end {
    display: flex;
    padding-left: 10px;
    align-items: center;
  }
  .nav-end-end > .ld {
    display: none;
  }

  .nav-inp {
    padding: 4px 8px;

    font-size: 11px;
    width: 70px;
  }

  nav a {
    margin-right: -10px;
  }

  .nav-btn {
    display: block;
  }
}

@media only screen and (max-width: 600px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
  }

  header nav {
    position: fixed;
    top: -100vh;
    left: 0;
    height: 100%;
    width: 106%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: var(--mainColor);
    transition: 1s;
  }

  header .responsive_nav {
    transform: translateY(100vh);
    background-color: black;
    z-index: 1;
  }

  nav {
    margin-top: -40px;
  }

  nav .nav-close-btn {
    position: absolute;
    top: 3rem;
    right: 3rem;
    font-size: x-large;
  }

  nav a {
    font-size: 1.5rem;
  }

  .nav-select {
    width: auto;
    text-align: center;
    padding: 3px 8px;
    font-size: small;
  }
  .nav-end-start i {
    font-size: 0.9rem;
  }
  .nav-end-end i {
    margin-right: 7px;
    font-size: medium;
  }
}
