/* ----------------- NavBar ------------------ */

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}















/* ----------------- Bredcrumb ------------------ */


.bc-bg {
  background-image: url(./images/bc-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  /* background-size: cover; */
  background-size: 100%;
}

.breadcrumb {
  /* opacity: ; */
  background-color: #0e1012d1;
  /* background: none; */
  height: 30vh;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
}
.bc-heading {
  font-size: xxx-large;
  font-weight: 800;
  font-family: "Nunito";
}
.bc-path span {
  color: red;
}

@media only screen and (max-width: 500px) {
  .breadcrumb {
    height: 15vh;
  }
  .bc-heading {
    font-size: xx-large;
  }
  .bc-path {
    font-size: small;
  }
}











/* ----------------- Contact ------------------ */

.contact-main {
  padding: 40px 100px;
}
.contact-top {
  padding-top: 80px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.c-t-col {
  display: flex;
  gap: 20px;
  align-items: center;
}

.c-t-icon {
  color: red;
  font-size: 2.5rem;
}

.c-t-t-h {
  font-weight: bold;
  font-size: large;
}
.c-t-t-t {
  font-size: 0.9rem;
  color: rgb(185, 185, 185);
}

/* ----------------- */

.contact-content {
  display: flex;
  padding: 50px 0;
  gap: 60px;
  align-items: center;
}

.c-c-map {
  display: flex;
  padding: 20px;
  border-radius: 30px;
  border: solid gray;
  background: black;
}

.c-c-map iframe {
  border: rgb(128, 128, 128) solid;
  border-radius: 15px;
  height: 450px;
  width: 600px;
}

.c-c-f-head-1 {
  color: red;
  font-weight: bolder;
  font-size: 1.8rem;

  max-width: 330px;
  /* position: relative; */
  font-family: "Nunito";
}

.c-c-f-head-1::after {
  font-family: "Nunito";
  content: "";
  display: block;
  width: 140px;
  height: 3px;
  border-radius: 10px;
  background: red;
  right: 30px;
  top: 45%;
  /* position: absolute; */
}

.c-c-f-head-2 {
  font-weight: 800;
  font-size: 2.2rem;
  font-family: "Nunito";
}

.c-c-f-head-2 span {
  color: red;
}

.c-c-f-head-3 {
  font-size: 1.2rem;
  font-weight: bold;
  color: rgb(214, 214, 214);
}
.c-c-f-head-3 {
  margin-top: 20px;
  font-size: 1rem;
  /* color: gray; */
  color: rgb(175, 175, 175);
  line-height: 22px;
}

.c-c-f-form {
  margin-top: 12px;
  background: rgb(0, 0, 0);
  /* color: green; */
}

.c-c-f-form > form {
  display: grid;
  gap: 10px;
}

/* ---------------ab--------------- */

.contact-mn {
  padding: 80px;
}

.contact-page {
  padding: 0 20px ;
  display: flex;
  gap: 60px;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.cont-map {
  border: gray solid;
  display: flex;
  padding: 20px;
  height: 450px;
  background-color: black;
  border-radius: 30px;
  width: 610px;
}

.cont-map iframe {
  border: rgb(137, 137, 137) 2px solid;
  width: 100%;
  border-radius: 15px;
}

.contact-col-2 {
  text-align: start;

}
.contact-col-2-h1 {
  color: red;
  font-weight: bolder;
  font-size: 1.8rem;
  font-family: "Nunito";
}

.contact-col-2-h2 {
  font-weight: 800;
  font-size: 2.2rem;
  font-family: "Nunito";
}

.contact-col-2-h2 span {
  color: red;
}

.contact-col-2-h3 {
  font-size: 1.2rem;
  font-weight: bold;
  color: rgb(214, 214, 214);
}

.contact-col-2-h4 {
  font-size: 1rem;
  color: rgb(175, 175, 175);
  line-height: 40px;
  margin-top: 20px;
}

.contact-frm {
  display: grid;
  gap: 10px;
}

.contact-frm > div {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.contact-frm input,
textarea {
  padding: 10px;
  border: none;
  font-family: poppins;
}
.contact-frm > button {
  font-family: poppins;
  font-size: large;
  font-weight: bold;
  background: red;
  color: white;
  padding: 8px;
  border: none;
}

@media only screen and (max-width: 500px) {
  .contact-main {
    padding: 20px;
  }

  .c-c-map iframe {
    height: 225px;
    width: 300px;
  }

  /* ---------------ab--------------- */

  .contact-top {
    padding: 40px 0;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px 0;
    display: grid;
    grid-template-columns: 1.6fr 1fr;
  }

  .c-t-col {
    display: flex;
    gap: 10px;
    align-items: center;
  }

  .c-t-icon {
    color: red;
    font-size: 1.4rem;
  }

  .c-t-t-h {
    font-weight: bold;
    font-size: 0.9rem;
  }
  .c-t-t-t {
    font-size: 0.7rem;
    color: rgb(185, 185, 185);
  }

  /* --------------- */

  .contact-mn {
    padding: 30px 10px;
  }
  .contact-page {
    padding: 0px;
    display: grid;
    gap: 20px;
    align-items: center;
    margin: auto;
  }

  .cont-map {
    width: 90%;
    margin: auto;
    padding: 10px;
    height: 200px;
  }

  .contact-col-2-h1 {
    font-size: larger;
  }

  .contact-col-2-h2 {
    font-size: 1.1rem;
  }

  .contact-col-2-h2 span {
    color: red;
  }

  .contact-col-2-h3 {
    font-size: .9rem;
  }

  .contact-col-2-h4 {
    font-size: 0.9rem;
    line-height: 30px;
    margin-top: 15px;
  }

  .contact-frm{
    width: 90%;
    margin: auto;
  }

  
.contact-frm > div {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}


  .contact-frm input,
  textarea {
    padding: 5px;
  }
}







 




/* ----------------- Footer ------------------ */


.footer-main {
  padding-top: 30px;
  background-color: black;
  color: white;
}

.footer-main a {
  /* color: rgb(215, 215, 215); */
  color: white;
  color: rgb(175, 175, 175);
  /* color: gray; */
  text-decoration: none;
}

/* head */
.footer-head {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 70px;
  /* padding: 90px; */
}

.fh-logo {
  text-align: center;
  border-right: solid 2px gray;
  padding-right: 80px;
}
.fh-logo a {
  color: white;
}

.fh-l-logo {
  font-size: 2rem;
  font-weight: 800;
  font-family: "Poppins";
  font-weight: 600;
}
.fh-l-logo span {
  color: red;
}
.fh-l-logo span i {
  font-size: 1.8rem;
}

.fh-l-tagline {
  font-size: large;
  color: rgb(198, 198, 198);
  letter-spacing: 4px;
  word-spacing: 2px;
  /* color: rgb(171, 41, 41); */
}

.fh-address-h {
  font-size: x-large;
  font-weight: bolder;
  color: red;
  font-family: "Nunito";
  font-weight: 800;
}

.fh-address-p {
  font-family: poppins;
  font-size: 1.1rem;
  font-weight: normal;
}

/* content */
.footer-content {
  padding: 50px 100px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer-content a:hover {
  color: red;
}

.fc-c-c {
  /* display: grid; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  /* width: 100%; */
  height: 150px;
}

.fc-c-h {
  color: red;
  font-family: cursive;
  font-weight: 1000;
  font-size: 1.3rem;
  /* position: relative; */
}

/* .fc-c-h::after {
    font-family: "Nunito";
    content: "";
    display: block;
    width: 150px;
    margin-top: 2px;
    height: 3px;
    background-color: red;
  } */

.fc-c-text {
  line-height: 25px;
}

.fc-c-form {
  padding: 3px;
  background: white;
  display: flex;
  justify-content: space-between;
}

.fc-c-form input {
  border: none;
  padding-left: 10px;
}
.fc-c-form button {
  color: white;
  border: none;
  background: red;
  padding: 10px;
  font-weight: bold;
}

.f-bfr > a::before {
  content: "\276f \276f";
  font-size: 0.7rem;
  margin-right: 10px;
  color: red;
  text-decoration: none !important ;
}

.f-bfr > div::before {
  content: "\2714";
  font-size: 0.7rem;
  margin-right: 10px;
  color: red;
  text-decoration: none !important ;
}

.f-bfr > div::before {
  /* content: " "; */
  margin-right: 13px;
}

.footer-cc {
  letter-spacing: 1px;
  word-spacing: 2px;
  text-align: center;
  padding: 15px 0;
}

@media only screen and (max-width: 800px) {
}
